import React, { useEffect, useState } from "react";
import style from "./CertificateModal.module.css";
import { Dialog } from "@mui/material";
import { useTranslation } from "react-i18next";
import { Notfication } from "../../../../../validation/Snackbar";
import { useSelector } from "react-redux";
import { useEditEditable } from "../../../../../hooks/institute/institute-staff-student-api";
import QLoader from "../../../../../Loader/QLoader";
import { useFileUpload } from "../../../../../hooks/authentication-api";
import JoinFormFileEdit from "../../../../../JoiningForm/Student/Form/JoinFormFileEdit";
import Crop from "../../../../../ImageCrop/Crop";
import PopupWrapper from "../../../../../MembersTab/staff/Mainbody/FinanceManager/FeesStructure/PopupWrapper";

function CertificateModal({
  addDisplay,
  hideModal,
  editableData,
  instituteId,
  viewAs,
  onRefetch,
}) {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [photoURL, setPhotoURL] = useState("");
  const [file, setFile] = useState("");
  const [openCrop, setOpenCrop] = useState(false);
  const [upload, setUpload] = useState(false);
  const [profilePhoto, setProfilePhoto] = useState("");

  const id = useSelector((state) => state.idChange.id);
  const [editable, setEditable] = useState({
    insAffiliated: editableData?.insAffiliated,
    insEditableText_one: editableData?.insEditableText_one,
    insEditableText_two: editableData?.insEditableText_two,
    affliatedLogo: editableData?.affliatedLogo,
  });
  const [notificationState, setNotificationState] = useState({
    msg: "",
    run: false,
  });
  const [editEditable] = useEditEditable();
  const [oneFileUpload] = useFileUpload();

  const onFileUpload = (e) => {
    if (e.target.files?.length) {
      const image = URL.createObjectURL(e.target.files[0]);
      setFile(e.target.files[0]);
      setProfilePhoto(image);
      setPhotoURL(image);
      setOpenCrop(true);
    }
  };

  const onUpload = (file) => {
    if (file) {
      setDisabled((pre) => !pre);

      const formData = new FormData();
      formData.append("file", file);
      oneFileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey) {
            setEditable((prev) => ({
              ...prev,
              affliatedLogo: res.data?.imageKey,
            }));
          }
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };

  useEffect(() => {
    if (upload) {
      onUpload(file);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [upload, file]);

  const AddEditableQuery = () => {
    if (
      editable.insAffiliated !== "" &&
      editable.insEditableText_one !== "" &&
      editable.insEditableText_two !== ""
    ) {
      setDisabled((pre) => !pre);

      editEditable({
        id: viewAs === "DESIGNATION_SIDE" ? instituteId : id,
        e_data: editable,
      }).then((res) => {
        if (res.data.status) {
          onRefetch();
          hideModal(false);
          setDisabled((pre) => !pre);
        }
      });
    } else {
      setNotificationState({
        msg: "Affiliation Required for Leaving Certificate",
        run: true,
      });
    }
  };

  return (
    <>
      <PopupWrapper open={addDisplay} onClose={() => hideModal(false)}>
        <div className={style.addDisplay}>
          <div className={style.title}>
            <h6>{t("menu")}</h6>
            <img
              src="/images/close-post-icon.svg"
              alt=""
              onClick={() => hideModal(false)}
            />
          </div>

          <div className={style.body}>
            <input
              type="text"
              maxLength="50"
              placeholder={t("aff_body")}
              name="insAffiliated"
              value={editable.insAffiliated}
              onChange={(e) =>
                setEditable({ ...editable, insAffiliated: e.target.value })
              }
            />

            <input
              type="text"
              maxLength="200"
              placeholder={t("ins_identity1")}
              name="insEditableText_one"
              value={editable.insEditableText_one}
              onChange={(e) =>
                setEditable({
                  ...editable,
                  insEditableText_one: e.target.value,
                })
              }
            />

            <input
              type="text"
              maxLength="88"
              placeholder={t("ins_identity2")}
              name="insEditableText_two"
              value={editable.insEditableText_two}
              onChange={(e) =>
                setEditable({
                  ...editable,
                  insEditableText_two: e.target.value,
                })
              }
            />

            <JoinFormFileEdit
              labelText={t("affiliated_logo")}
              name={"affliatedLogo"}
              value={editable.affliatedLogo ? editable.affliatedLogo : ""}
              onChange={onFileUpload}
              customStyleContainer={{
                width: "100%",
              }}
            />
          </div>
          {editable.insAffiliated &&
          editable.insAffiliated !== "" &&
          editable.insEditableText_one &&
          editable.insEditableText_one !== "" &&
          editable.insEditableText_two &&
          editable.insEditableText_two !== "" ? (
            <div className={style.footer} onClick={AddEditableQuery}>
              <div className={style.btn}>{t("save_menu")}</div>
            </div>
          ) : (
            <div className={`${style.footer}`}>
              <div className={`${style.btn} ${style.footerdisabled}`}>
                {t("save_menu")}
              </div>
            </div>
          )}
        </div>{" "}
        {disabled && <QLoader />}
        <Dialog open={openCrop}>
          <Crop
            setPhotoURL={setPhotoURL}
            photoURL={photoURL}
            setFile={setFile}
            setOpenCrop={setOpenCrop}
            setUpload={setUpload}
            setProfilePhoto={setProfilePhoto}
            first={1}
            second={1}
          />
        </Dialog>
      </PopupWrapper>
      <Notfication
        msg={notificationState.msg}
        run={notificationState.run}
        setRun={() => setNotificationState({ msg: "", run: false })}
        postiton="bottom"
        type="error"
      />
    </>
  );
}

export default CertificateModal;
